@font-face {
  font-family: 'Hitmarker-Text';
  src: url('/public/hitmarkertext.ttf') format('truetype');
}
.container-slider {
    position: relative;
    top: 3.5rem;
    height: 333px;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-top: 0.5px solid rgba(255, 255, 255, 0.4);
    border-bottom: 0.5px solid rgba(255, 255, 255, 0.4);
    overflow: hidden;
  }
  
  .header-detail {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 85%;
    border-bottom: 0.5px solid rgba(255, 255, 255, 0.4);
    color: #fff;
  }
  
  .filter-part {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }
  
  .filters {
    margin-right: 1rem;
    font-size: .8rem;
  }
  
  .no {
    width: 30px;
    height: 30px;
    background: none;
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    cursor: pointer;
    margin: 0 .3rem;
    border: none;
    outline: none;
  }
  
  
  .view-all {
    background-color: #fff;
    padding: .4rem .6rem;
  }
  
  .view-all a {
    color: #000;
    text-decoration: none;
    font-size: .8rem;
    font-weight: 600;
    text-transform: lowercase;
  }
  
  .slider-main {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    width: 100%;
  }
  
  .sl {
    width: 30px;
    margin: 1rem;
  }
  
  .sl img {
    width: 25px;
  }
  
  .slider-tainer {
    width: 1200px;
    height: 250px;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .slider-track {
    display: flex;
    width: calc(200px * 12); /* Adjust based on the number of items and their width */
    animation: scroll 20s linear infinite;
  }
  
  @keyframes scroll {
    0% {
      transform: translateX(0);
    }
    100% {
      transform: translateX(-50%);
    }
  }
  
  .slider-track > div {
    flex: 0 0 auto;
    width: 200px; /* Increased width for larger images */
    margin: 0 10px; /* Adjust based on your design */
  }
  
  .right-arrow {
    transform: rotate(180deg);
  }
  .sponsorstitle{
    font-family: 'Hitmarker-Text-Card', sans-serif;
    font-size: 2rem;
    color: #a59d95;
  }
  /***** responsive codes *****/

@media screen and (max-width: 1450px) {
    .slider-tainer {
      width: 1000px;
    }
  }
  
  @media screen and (max-width: 1220px) {
    .slider-tainer {
      width: 800px;
    }
  }
  
  @media screen and (max-width: 960px) {
    .slider-tainer {
      width: 600px;
    }
  }
  
  @media screen and (max-width: 800px) {
    .container-slider {
      top: 80rem; /* Adjust this value as needed to prevent overlap */
    }
  }
  
  @media screen and (max-width: 750px) {
    .slider-tainer {
      width: 550px;
    }
  
    .sl img {
      width: 20px;
    }
  }
  
  @media screen and (max-width: 680px) {
    .slider-tainer {
      width: 300px;
    }
  }
  
  @media screen and (max-width: 590px) {
    .filters {
      font-size: .7rem;
    }
  
    .no {
      width: 20px;
      height: 20px;
    }
  
    .view-all {
      font-size: .5rem;
    }
  }
  
  @media screen and (max-width: 490px) {
    .filters {
      font-size: .5rem;
    }
  
    .no {
      width: 15px;
      height: 15px;
    }
  
    .view-all a {
      font-size: .6rem;
    }
  
    .header-detail {
      font-size: .7rem;
    }
  }
  
  @media screen and (max-width: 430px) {
    .slider-tainer {
      width: 250px;
    }
  }
  
  @media screen and (max-width: 380px) {
    .filters {
      font-size: .4rem;
    }
  
    .no {
      width: 10px;
      height: 10px;
    }
  
    .view-all a {
      font-size: .5rem;
    }
  
    .header-detail {
      font-size: .6rem;
    }
  
    .slider-tainer {
      width: 200px;
    }
  
    .sl {
      width: 10px;
    }
  
    .sl img {
      width: 10px;
    }
  }
  
  @media screen and (max-width: 330px) {
    .filters {
      display: none;
    }
  
    .no {
      display: none;
    }
  
    .view-all a {
      font-size: .6rem;
    }
  
    .header-detail {
      font-size: .7rem;
    }
  
    .slider-tainer {
      width: 150px;
    }
  }